	module.exports = {"data":{"allMarkdownRemark":{"edges":[{"node":{"html":"<p>background-image: url(<a href=\"http://www.valleytechnicalacademy.com/wp-content/uploads/2017/10/VTA-logo-350-1.png\">http://www.valleytechnicalacademy.com/wp-content/uploads/2017/10/VTA-logo-350-1.png</a>)</p>\n<hr>\n<h2>The Adventure Begins…</h2>\n<hr>\n<h2>Todays Agenda</h2>\n<ul>\n<li>Introduce Staff</li>\n<li>Talk About Membership Perks</li>\n<li>Get To Know Each Other</li>\n<li>Discuss Day-To-Day Workflow</li>\n</ul>\n<hr>\n<p>class: center</p>\n<h2>We Are Valley Tech</h2>\n<h3>Founder Jim Smith</h3>\n<p><img src=\"https://media.licdn.com/dms/image/C4D03AQGuzz_FGzwnZA/profile-displayphoto-shrink_800_800/0?e=1534377600&#x26;v=beta&#x26;t=gA0OsLU775A_SaTuzR3H07E1mcb3S7b_Wltha25NTwE\" alt=\"Jim Smith\"></p>\n<hr>\n<p>class: center</p>\n<h2>We Are Valley Tech</h2>\n<h3>CEO Ernesto Ayala</h3>\n<h2><img src=\"https://media.licdn.com/dms/image/C4E03AQE1IaaeXzSrwA/profile-displayphoto-shrink_800_800/0?e=1534377600&#x26;v=beta&#x26;t=nfnwk4F2RfXIL2XGfyV0zVf9N5tKctuHNecqKNAuJv4\" alt=\"Ernesto Ayala\"></h2>\n<p>class: center</p>\n<h2>We Are Valley Tech</h2>\n<h3>CMO Joshua Garza</h3>\n<h2><img src=\"https://media.licdn.com/dms/image/C4E03AQF-qoMAHrDkNw/profile-displayphoto-shrink_800_800/0?e=1534377600&#x26;v=beta&#x26;t=1bCz5KgRGT989YO5s0TcQ8BqSX6PWvflQnVbAfBe0xc\"></h2>\n<p>class: center</p>\n<h2>Membership Perks</h2>\n<h3>Access Anytime</h3>\n<h2><img src=\"https://pbs.twimg.com/media/Cqp-nt2VMAEw7Q0.jpg\"></h2>\n<p>class: center</p>\n<h2>Membership Perks</h2>\n<h3>Break Room + Lockers</h3>\n<h2><img src=\"http://missiontexas.us/wp-content/uploads/2016/11/15000183_1268951579823073_7134681254153957549_o.jpg\"></h2>\n<p>class: center</p>\n<h2>Membership Perks</h2>\n<h3>Office Hours 4:30 - 5:30pm Tuesday/Thursday</h3>\n<h2><img src=\"https://www.pearsoned.com/wp-content/uploads/Thomas_Putman-Office_Hours-770x370.jpg\"></h2>\n<h2>Let’s Get To Know Each Other</h2>\n<ul>\n<li>Name</li>\n<li>Location</li>\n<li>Dream Job in Tech/App Idea</li>\n</ul>\n<hr>\n<p>class: center</p>\n<h2>About Me</h2>\n<img src=\"https://pbs.twimg.com/media/De2igdLVAAISaVx.jpg\" width=\"500px\">\n<h3>Full Stack Developer for 4 &#x26; half years (2014 - Today)</h3>\n<hr>\n<h2>About Me</h2>\n<ul>\n<li>Bachelors at Texas State University (Electronic Media)</li>\n<li>Graduated Codeup Coding Bootcamp - San Antonio (2014)</li>\n<li>Taught Web Development at UT-Austin Coding Bootcamp</li>\n<li>Into making music; have my own music on Spotify/iTunes</li>\n<li>Obsessed with esports (League of Legends)</li>\n</ul>\n<h2><img class=\"float:left\" src=\"https://d3kjdix9ozghcx.cloudfront.net/coverimages/large_entry-cover-97068adb150dbc0c218597b8f443b266.jpg\" width=\"700px\"/></h2>\n<p>class: center</p>\n<h2>What I’ve Built</h2>\n<div>\n          <div\n            class=\"gatsby-resp-iframe-wrapper\"\n            style=\"padding-bottom: 56.25%; position: relative; height: 0; overflow: hidden;margin-bottom: 1.0725rem\"\n          >\n            <iframe src=\"https://www.youtube.com/embed/rnDznJiAl_Y\" frameborder=\"0\" allow=\"autoplay; encrypted-media\" allowfullscreen style=\"\n            position: absolute;\n            top: 0;\n            left: 0;\n            width: 100%;\n            height: 100%;\n          \"></iframe>\n          </div>\n          </div>\n<hr>\n<p>class: center</p>\n<h2>What I’ve Built</h2>\n<div>\n          <div\n            class=\"gatsby-resp-iframe-wrapper\"\n            style=\"padding-bottom: 56.25%; position: relative; height: 0; overflow: hidden;margin-bottom: 1.0725rem\"\n          >\n            <iframe src=\"https://www.youtube.com/embed/SOpszWgA3xg\" frameborder=\"0\" allow=\"autoplay; encrypted-media\" allowfullscreen style=\"\n            position: absolute;\n            top: 0;\n            left: 0;\n            width: 100%;\n            height: 100%;\n          \"></iframe>\n          </div>\n          </div>\n<hr>\n<p>class: center</p>\n<h2>What I’ve Built</h2>\n<p><a href=\"https://www.bravelets.com\">https://www.bravelets.com</a></p>\n<p><a href=\"http://parlevelsystems.com\">http://parlevelsystems.com</a></p>\n<p><a href=\"http://limeadelabs.com/\">http://limeadelabs.com/</a></p>\n<p><img src=\"https://media.glassdoor.com/sqll/1005015/bravelets-squarelogo-1458591811984.png\">\n<img src=\"http://www.siliconhillsnews.com/wp-content/uploads/2013/07/images-9.jpeg\"></p>\n<hr>\n<h2>Day-To-Day Week 1-6</h2>\n<h3>9:30 - 12:30 - Lecture &#x26; Exercises, Morning Coding Warmups</h3>\n<br>\n<h3>12:30 - 1:30 - LUNCH</h3>\n<br>\n<h3>1:30 - 4:30 - More Lecture &#x26; Exercises + Activities</h3>\n<hr>\n<h2>After Hours - Homework!</h2>\n<h3>LMS Learning</h3>\n<h3>10 Lessons A Week</h3>\n<h2><img src=\"https://imgur.com/TC6XESp\"></h2>\n<p>class: center</p>\n<h2>Weekly Project (Usually introduced on Monday, Due Sunday)</h2>\n<h2><img src=\"https://resources.api.exeterlms.com/Resources/7c622c65-97af-4e34-b090-3b9738c47ba3?access_token=a064bc404ce21034143e295fad5c3cda1f7dc60d9fc9cec0ab7a39ff858d0d87\"></h2>\n<p>class: center</p>\n<h2>Day-To-Day Workflow Weeks 7-12</h2>\n<h3>Capstone Project</h3>\n<h3>Teams of 3-5 people</h3>\n<h2><img src=\"https://i.imgur.com/qjYjTWC.jpg\" width=\"400px\"/></h2>\n<p>class: middle center</p>\n<h1>Questions?</h1>","internal":{"content":"---\ntitle: \"DAY 0 - We Are Valley Tech\"\ncourseId:  \"CFSG105\"\ncirriculum:\n  - \"Web / Full Stack / Software Development\"\ntype: \"slide\"\ndraft: false\n---\nbackground-image: url(http://www.valleytechnicalacademy.com/wp-content/uploads/2017/10/VTA-logo-350-1.png)\n\n---\n## The Adventure Begins...\n\n\n---\n## Todays Agenda\n\n- Introduce Staff\n- Talk About Membership Perks\n- Get To Know Each Other\n- Discuss Day-To-Day Workflow\n\n---\nclass: center\n## We Are Valley Tech\n### Founder Jim Smith\n\n![Jim Smith](https://media.licdn.com/dms/image/C4D03AQGuzz_FGzwnZA/profile-displayphoto-shrink_800_800/0?e=1534377600&v=beta&t=gA0OsLU775A_SaTuzR3H07E1mcb3S7b_Wltha25NTwE)\n\n---\nclass: center\n## We Are Valley Tech\n### CEO Ernesto Ayala\n![Ernesto Ayala](https://media.licdn.com/dms/image/C4E03AQE1IaaeXzSrwA/profile-displayphoto-shrink_800_800/0?e=1534377600&v=beta&t=nfnwk4F2RfXIL2XGfyV0zVf9N5tKctuHNecqKNAuJv4)\n---\nclass: center\n## We Are Valley Tech\n### CMO Joshua Garza\n![](https://media.licdn.com/dms/image/C4E03AQF-qoMAHrDkNw/profile-displayphoto-shrink_800_800/0?e=1534377600&v=beta&t=1bCz5KgRGT989YO5s0TcQ8BqSX6PWvflQnVbAfBe0xc)\n---\nclass: center\n## Membership Perks\n### Access Anytime\n![](https://pbs.twimg.com/media/Cqp-nt2VMAEw7Q0.jpg)\n---\nclass: center\n## Membership Perks\n### Break Room + Lockers\n![](http://missiontexas.us/wp-content/uploads/2016/11/15000183_1268951579823073_7134681254153957549_o.jpg)\n---\nclass: center\n## Membership Perks\n### Office Hours 4:30 - 5:30pm Tuesday/Thursday\n![](https://www.pearsoned.com/wp-content/uploads/Thomas_Putman-Office_Hours-770x370.jpg)\n---\n\n## Let's Get To Know Each Other\n- Name\n- Location\n- Dream Job in Tech/App Idea\n\n---\nclass: center\n## About Me\n<img src=\"https://pbs.twimg.com/media/De2igdLVAAISaVx.jpg\" width=\"500px\">\n\n### Full Stack Developer for 4 & half years (2014 - Today)\n\n---\n## About Me\n\n- Bachelors at Texas State University (Electronic Media)\n- Graduated Codeup Coding Bootcamp - San Antonio (2014)\n- Taught Web Development at UT-Austin Coding Bootcamp\n- Into making music; have my own music on Spotify/iTunes\n- Obsessed with esports (League of Legends)\n\n<img class=\"float:left\" src=\"https://d3kjdix9ozghcx.cloudfront.net/coverimages/large_entry-cover-97068adb150dbc0c218597b8f443b266.jpg\" width=\"700px\"/>\n---\nclass: center\n## What I've Built\n<iframe width=\"560\" height=\"315\" src=\"https://www.youtube.com/embed/rnDznJiAl_Y\" frameborder=\"0\" allow=\"autoplay; encrypted-media\" allowfullscreen></iframe>\n\n---\nclass: center\n## What I've Built\n<iframe width=\"560\" height=\"315\" src=\"https://www.youtube.com/embed/SOpszWgA3xg\" frameborder=\"0\" allow=\"autoplay; encrypted-media\" allowfullscreen></iframe>\n\n---\nclass: center\n## What I've Built\n\nhttps://www.bravelets.com\n\nhttp://parlevelsystems.com\n\nhttp://limeadelabs.com/\n\n![](https://media.glassdoor.com/sqll/1005015/bravelets-squarelogo-1458591811984.png)\n![](http://www.siliconhillsnews.com/wp-content/uploads/2013/07/images-9.jpeg)\n---\n\n## Day-To-Day Week 1-6\n\n### 9:30 - 12:30 - Lecture & Exercises, Morning Coding Warmups\n\n<br>\n\n### 12:30 - 1:30 - LUNCH\n\n<br>\n\n### 1:30 - 4:30 - More Lecture & Exercises + Activities\n---\n## After Hours - Homework!\n### LMS Learning\n### 10 Lessons A Week\n\n![](https://imgur.com/TC6XESp)\n---\nclass: center\n## Weekly Project (Usually introduced on Monday, Due Sunday)\n![](https://resources.api.exeterlms.com/Resources/7c622c65-97af-4e34-b090-3b9738c47ba3?access_token=a064bc404ce21034143e295fad5c3cda1f7dc60d9fc9cec0ab7a39ff858d0d87)\n---\nclass: center\n## Day-To-Day Workflow Weeks 7-12\n### Capstone Project\n### Teams of 3-5 people\n<img src=\"https://i.imgur.com/qjYjTWC.jpg\" width=\"400px\"/>\n---\n\nclass: middle center\n# Questions?\n"},"fields":{"slug":"/courses/coding-from-scratch/slides/day0-intro/"}}}]}},"pathContext":{"slug":"/courses/coding-from-scratch/slides/day0-intro/"}}

